import youbian from "@/assets/gengduo2.png";
export default {
  data() {
    return {
      icon: youbian,
      topTitle: "公告",
      list: []
    };
  },
  filters: {
    formatDate: function(value) {
      let val = value * 1000;
      let date = new Date(val);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let m = date.getMinutes();
      m = m < 10 ? "0" + m : m;
      let s = date.getSeconds();
      s = s < 10 ? "0" + s : s;
      return y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;
    }
  },
  mounted() {
    this.getNotice();
  },
  methods: {
    getNotice() {
      var that = this;
      that
        .$request({
          method: "POST",
          url:"/index/notice"
        })
        .then(function(response) {
          that.list = response.data;
        })
        .catch(function(error) {});
    }
  }
};



